@import url('@pmndrs/branding/styles.css');

* {
  box-sizing: border-box;
}

@font-face {
  font-family: "BebasNeue";
  src: local("BebasNeue"),
   url("./fonts//BebasNeue/BebasNeue-Regular.ttf") format("truetype");
  font-weight: bold ;
  }


@font-face {
  font-family: "Anton";
  src: local("Anton"),
   url("./fonts//Anton//Anton-Regular.ttf") format("truetype");
  font-weight: bold ;
  }

  
@font-face {
  font-family: "Roboto";
  src: local("Roboto"),
   url("./fonts//Roboto/Roboto-Regular.ttf") format("truetype");
  font-weight: bold ;
  }

  @font-face {
    font-family: "BigShoulders";
    src: local("BigShoulders"),
     url("./fonts//BigShoulders/BigShouldersText-Regular.ttf") format("truetype");
    font-weight: bold ;
    }

html,
body,
#root {
  width: 100vw;
  height: 100%;
  margin: 0;
  padding: 0;
  /* overflow: scroll; */
}

body {
  font-family: 'Inter var', sans-serif;
  color: white;
  /* background: #9EFF00; */
  background: rgb(212,255,0);
  background: linear-gradient(0deg, rgba(212,255,0,1) 15%, rgba(237,239,247,1) 79%);
}



.fullscreen {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 1s;
}

.background {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: -1;
}

.bg {
  background: linear-gradient(to top, #0a0a0a, #181a1a, #1d1d1f);
}

.notready {
  color: #606060;
}

.clicked {
  pointer-events: none;
  opacity: 0;
}

a {
  pointer-events: all;
  cursor: pointer;
  color: inherit;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
  color: inherit;
}

.font-face-bn {
  font-family: "BebasNeue";
 }

 .font-face-an {
  font-family: "Anton";
 }

 .font-face-ro {
  font-family: "Roboto";
 }

 .font-face-bs {
  font-family: "BigShoulders";
 }
 .text-shadow {
  text-shadow: 3px 2px 2px rgba(0,0,0,1);
 }
